.api-button{
     background-color: #FFFFFF;
     font-size: 18px;
     font-family: "Montserrat";
     font-weight: 600;
     text-align: center;
     color: #202D62;
     line-height: 24px;
     padding: 10px 32px;
     float: right;
     border-radius: 4px;
}

@media(max-width:768px){
    .api-button{
        float: right;
        font-size: 12px;
        padding: 4px;
        border-radius: 4px;
    }
}

.api h2{
    text-align: center;
    margin-bottom: 24px;
    text-transform: uppercase;
}

.API-Section{
    background: #E2E9F1;
}

#below-head{
    background-color: #e9e9e9;
    padding: 15px;
}

#below-head-values{
    background-color: #C4DFF4;
}

.api-table>thead>tr>th {
    padding: 15px;
    background-color: #202D62;
    color: white;
}

.api-table{
    margin-bottom: 34px;
}

.api-table>thead>tr>th{
    line-height: inherit;
}

.table>thead>tr>th{
    vertical-align: inherit;
}

.api-table>tbody>tr>th{
    padding: 15px;
}
.api-table>tbody>tr>td{
    padding: 15px;
}

.flex-container{
    display: flex;
    flex-flow:row wrap;
    background-color: #202D62;
    padding: 8px;
    align-items: center;
}
.flex-container1{
    display: flex;
    flex-flow:row wrap;
    background-color: #e9e9e9;
    padding: 8px;
}
.flex-container2{
    display: flex;
    flex-flow:row wrap;
    background-color: #C4DFF4;
    padding: 8px;
}
.flex-item{
    margin: 5px;
    font-size: 18px;
    font-family: "Montserrat", Sans-serif;
    font-weight: 600;
    flex-grow: 1;
    flex-shrink:0;
    flex-basis:20%;
    color: white;
}
.flex-item1{
    margin: 5px;
    font-size: 18px;
    font-family: "Montserrat", Sans-serif;
    font-weight: 600;
    flex-grow: 1;
    flex-shrink:0;
    flex-basis:20%;
    color: #202D62;
}

@media (max-width:768px){
    .flex-item{
        font-size: 12px;
        font-weight: 600;
    }
}
@media (max-width:768px){
    .flex-item1{
        font-size: 12px;
        font-weight: 600;
    }
}

.fg3{
    flex-grow: 3;
}

.api-flex{
    margin-bottom: 30px;
}
